export interface ProfileModel {
  userName: string;
  image?: string;
  hideAvatar?: boolean;
  typeLong?: boolean;
  inverted?: boolean;
  desc?: string;
  header?: string;
  prefixId?: string;
  view: {
    title?: string;
    link?: string;
    target?: string;
    id?: string;
    // TODO: Refactor this if possible
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    action?(event: any): any;
  };
  signOut: {
    title?: string;
    link?: string;
    id?: string;
    // TODO: Refactor this if possible
    /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
    action?(event: any): any;
  };
  menu: EnumProfileItem[];
  formatter?(name: string): string;
}

export interface EnumProfileItem  {
  name: string;
  className: string;
  link?: string;
  disabled?: boolean;
  separated?: boolean;
  active?: boolean;
  target?: string;
  hide?: boolean;
  id?: string;
  // TODO: Refactor this if possible
  /* eslint-disable-next-line @typescript-eslint/no-explicit-any */
  action?(event: any): any;
}
